.cta-button {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  padding: 10px 20px;
  background: #0c5bff;
  color: white;
  text-decoration: none;
  font-size: 17px;
  font-weight: 600;
  text-transform: lowercase;
  border-radius: 9999px;
  transition: all 0.3s ease;
  box-shadow: 0 0 40px rgba(12, 91, 255, 0.55);
  border: 1px solid rgba(255, 255, 255, 0.12);
}
.cta-button:hover {
  background: #0a46d4;
  box-shadow: 0 0 55px rgba(12, 91, 255, 0.75);
}
.cta-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  background: rgba(255, 255, 255, 0.15);
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, 0.15);
  box-shadow: 0 0 30px rgba(12, 91, 255, 0.45);
  transition: transform 0.3s ease;
}
.cta-icon:hover {
  transform: rotate(45deg);
}
.portfolio-hero-glow-primary {
  background: radial-gradient(ellipse 60% 50% at 15% 15%,
      var(--color-primary-faint),
      transparent 50%);
}
.portfolio-hero-glow-accent {
  background: radial-gradient(ellipse 50% 40% at 85% 10%,
      var(--color-accent-faint),
      transparent 45%);
}
.portfolio-hero-glow-purple {
  background: radial-gradient(circle at 5% 50%,
      rgba(135, 5, 242, 0.06),
      transparent 30%);
}
.portfolio-hero-vignette {
  background: radial-gradient(ellipse 80% 80% at 50% 50%,
      transparent 30%,
      var(--color-ghost-vignette) 100%);
}
.portfolio-hero-scroll-line {
  background: linear-gradient(to bottom, var(--color-text-60), transparent);
}
.video-overlay {
  background: radial-gradient(120% 120% at 70% 30%,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0.55) 70%,
      rgba(0, 0, 0, 0.75) 100%);
}
